.contact_section h1 {
  text-align: center;
  width: 100%;
  padding: 20px;
}
.contact_section iframe {
  margin: 40px 0;
}
.contact_details {
  display: flex;
  justify-content: space-around;
}
.contact_details p {
  padding: 10px;
  width: 300px;
  color: #58595b;
  font-weight: 500;
}
.social p {
  font-size: 20px;
  color: #58595b;
  font-weight: 600;
  padding: 20px;
}
.icons {
  margin-right: 10px;
}
